import { cards } from './cards.js'
import './css/page.css'
export function renderLevelPage(box, cardsCount) {
    let openHtml = ''
    let closeHtml = ''
    for (let i = 0; i < cardsCount; i++) {
        const random = cards[Math.floor(Math.random() * cards.length)]
        openHtml += `
        <div class="card data-index=${i}">
            <input
                type="image"
                name="image"
                src="src/css/asset/jpg/${random}.jpg"
                alt="${random}"
                width="95px"
                height="133px"
            />
        </div>`
        closeHtml += `
        <div class="card data-index=${i}">
        <input
        type="image"
        name="image"
        src="src/css/asset/jpg/рубашка.jpg"
        alt="рубашка"
        width="95px"
        height="133px"
        />
        </div>`
        cards.splice(cards.indexOf(random), 1)
    }
    const headHtml = `
    <header class="head">
    <div class="timetable">
        <div class="minutes">
            <div class="min">
                <p>min</p>
            </div>
            <div class="min">
                <p>sek</p>
            </div>
        </div>
        <div class="time">
            <time>00.00</time>
        </div>
    </div>
    <div class="startAgain">
        <button class="buttonstart">Начать заново</button>
    </div>
</header>`
    const close = `<div class="tops">${headHtml}
 <div class="cards">${closeHtml}</div> </div>`
    const open = `<div class="tops">${headHtml}
 <div class="cards">${openHtml}</div> </div>`
    box.innerHTML = open
    setTimeout(() => {
        box.innerHTML = close
        const card = document.querySelectorAll('.card')
        let firstCard = ''
        for (const car of card) {
            car.addEventListener('click', (event) => {
                const clickCard = event.currentTarget
                const index = clickCard.getAttribute('data-index')
                const selectCard = cards[index]
                clickCard.innerHTML = `<img src="src/css/asset/jpg/${selectCard}.jpg" alt="${selectCard}">`
                if (firstCard === null) {
                    firstCard === clickCard
                } else {
                    const secondCard = firstCard.getAttribute('data-index')
                    const secondCardIndex = cards[secondCard]
                    if (selectCard.charAt(0) === secondCardIndex.charAt(0)) {
                        alert('Вы победили')
                    } else {
                        firstCard.innerHTML = ``
                        secondCard.innerHTML = ``
                        firstCard = null
                    }
                }
            })
        }
    }, 3000)
}
